.button {
    display: inline-block;
    border: 0;
    font: inherit;
    cursor: pointer;
    text-decoration: none;
    min-width: 140px;
    border-radius: 5px;    
    padding: 8px 20px;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    text-align: center;
    color: #ffffff;
    background-color: #C70101;

    &:hover {
        color: #C70101;
        background-color: #ffffff;
        outline: 1px solid #C70101;
    }
}


@media (min-width: $tablet-width) {
    .button {
        font-size: 16px;
        line-height: 24px;
        padding: 12px 40px;
    }
}    

