.container {
    width: calc(100% - 40px);
    margin: 0 auto;
    padding-top: 20px;
}

@media (min-width: $tablet-width) {
    .container {
        width: 92%;
        padding-left: 30px;
        padding-right: 30px;
    }
}  

@media (min-width: $desktop-width) {
    .container {
        width: 942px;
        padding-left: 30px;
        padding-right: 30px;
      }      
}    