.promo {
    text-align: center;
}

.promo__text {
    padding-top: 30px;
    margin-bottom: 30px;
    text-align: center;
}

.promo__title {
    font-weight: bold;
    font-size: 30px;
    line-height: 30px;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
}

.promo__slogan {
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}

.promo__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.promo__item {
    position: relative;
    font-size: 14px;
    line-height: 16px;
    padding-top: 65px;
    margin-bottom: 30px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background-size: contain;
        background-repeat: no-repeat;
    }
}

.promo__item--time::before {
    width: 42px;
    height: 46px;
    background-image: url("../img/timer.svg");    
}

.promo__item--delivery::before {
    width: 44px;
    height: 45px;
    background-image: url("../img/app.svg");    
}

.promo__item--answers::before {
    width: 45px;
    height: 42px;
    background-image: url("../img/messages.svg");    
}

.promo__link {
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: underline;

    &:hover {
        color: #C70101;
    }
}

.promo__button {
    margin-bottom: 20px;
}

.promo__go {
    padding: 0;
    padding-bottom: 50px;
    margin: 0;
    font-size: 14px;
    line-height: 18px;
}

@media (min-width: 550px) {
    .promo__list {
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 500px;
        margin: 0 auto;
    }
}    

@media (min-width: $tablet-width) {
   .promo__text {
        padding-top: 0;
        text-align: right;
        margin-bottom: 57px;
   }

    .promo__title {
        font-size: 42px;
        line-height: 42px;
    }
    
    .promo__slogan {
        font-size: 24px;
        line-height: 24px;
    }  
    
    .promo__wrap {
        width: 520px;
        margin-left: auto;
    }

    .promo__button {
        padding: 12px 40px;
        font-size: 16px;
        line-height: 24px;
    }

    .promo__go {        
        font-size: 16px;
        line-height: 20px;
        padding-bottom: 40px;
    }
}    

@media (min-width: $desktop-width) {
    .promo {
        text-align: right;
        margin-left: auto;
    }   
    
    .promo__list {
        width: 430px;
        margin: 0;
        padding-right: 20px;
        margin-left: auto;
        margin-bottom: 30px;
        text-align: center;
    }

    .promo__item span {
        display: block;
    }    
}