.form {
    max-width: 303px;
    margin: 0 auto;
}

.form__section {
    margin-bottom: 20px;
}

.form__text {
    border: 1px solid rgba(205,205,205,.5);
    color: #4B4B4B;
    height: 45px;    
    padding-left: 10px;
    border-radius: 5px;
    width: 100%;
    box-sizing: border-box;

    &::placeholder {
        color: #4B4B4B;
    }
}

.form__field {
    margin-bottom: 15px;
}

.form__send {
    width: 100%;    
}

@media (min-width: $tablet-width) {
    .form {
        margin: 0;
        margin-right: auto;
    }
}    