.header {
    background-color: #f6f6f6;
    padding-top: 20px;
}

.header__promo {
    background-image: url("../img/bg.jpg");
    background-position: 40% center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg@2x.jpg");
        background-size: auto 650px;
    }     
}

.header__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
}

.header__img {
    display: block;
    margin-bottom: 25px;
}

.header__company {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 25px;
}

.header__callback {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__link {
    color: #0D0D0D;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 20px;
}

.header__oficial {
    font-size: 12px;
    line-height: 14px;
    margin-top: 0;
    margin-bottom: 10px;
    padding: 0;
}

.header__company-name {
    font-size: 16px;
    line-height: 18px;
    margin: 0;
    padding: 0;
}

.header__button.button {
    padding: 10px 5px;
    font-size: 12px;
    line-height: 17px;
}

@media (min-width: $tablet-width) {
    .header {    
        padding-top: 0;
        background-color: green;
        background-image: url("../img/bg.jpg");
        background-position: 40% center;
        background-repeat: no-repeat;
        background-size: cover;
        color: #ffffff;    
        height: 665px;
    }

    .header__promo {
        background-image: none;
        background-color: transparent;
    }

    .header__info {
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 70px;
    }

    .header__img {
        margin-bottom: 0;
    }    

    .header__company {
        margin-bottom: 0;
    }

    .header__company-name {
        font-size: 18px;
        line-height: 20px;
    }

    .header__callback {
        padding-top: 15px;
    }

    .header__link {       
        color: #ffffff; 
        font-size: 18px;
        line-height: 20px;
        margin-bottom: 5px;
    }    

}


@media (min-width: $desktop-width) {
   .header {
      background-position: center center;
   }  

   .header__callback {
    padding-left: 85px;
    }
    
}