.advantages {
    background-color: #F6F6F6;
    text-align: center;
    padding-top: 20px;
}

.advantages__title {
    font-size: 20px;
    line-height: 20px;
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
}

.advantages__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

.advantages__item {
    position: relative;
    padding-top: 95px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 20px;
    box-sizing: border-box;    

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 70px;
        height: 70px;
        background-position: center;
    }
}

.advantages__text {
    font-family: "Moderno-medium";
    font-size: 16px;
    line-height: 18px;
}

.advantages__item--practice::before {
    background-image: url("../img/cup.svg");
    background-size: contain;
    background-repeat: no-repeat;
}

.advantages__item--health::before {
    background-image: url("../img/heart.svg");
    background-size: contain;
    background-repeat: no-repeat;
}

.advantages__item--run::before {
    background-image: url("../img/shoe.svg");
    background-size: contain;
    background-repeat: no-repeat;
}

.advantages__header {
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
}

.advantages__link {
    color: #C70101;
    font-weight: normal;
    text-transform: uppercase;
}

@media (min-width: $tablet-width) {
   .advantages {
    padding-top: 55px;
    padding-bottom: 30px;
   }

   .advantages__title {
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 60px;
   }

    .advantages__list {
        margin: 0 auto;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }    

    .advantages__item {
        padding-top: 108px;
        width: 30%;     
    }
}

