@font-face {
  font-family: "Moderno";
  src: url("../fonts/moderno.woff2") format("woff2"), url("../fonts/moderno.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Moderno-light";
  src: url("../fonts/moderno-light.woff2") format("woff2"), url("../fonts/moderno-light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Moderno-medium";
  src: url("../fonts/moderno-medium.woff2") format("woff2"), url("../fonts/moderno-medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Moderno", "Arial", sans-serif;
  color: #0D0D0D;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: calc(100% - 40px);
  margin: 0 auto;
  padding-top: 20px;
}

@media (min-width: 768px) {
  .container {
    width: 92%;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 942px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.header {
  background-color: #f6f6f6;
  padding-top: 20px;
}

.header__promo {
  background-image: url("../img/bg.jpg");
  background-position: 40% center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header__promo {
    background-image: url("../img/bg@2x.jpg");
    background-size: auto 650px;
  }
}

.header__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.header__img {
  display: block;
  margin-bottom: 25px;
}

.header__company {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px;
}

.header__callback {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__link {
  color: #0D0D0D;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 20px;
}

.header__oficial {
  font-size: 12px;
  line-height: 14px;
  margin-top: 0;
  margin-bottom: 10px;
  padding: 0;
}

.header__company-name {
  font-size: 16px;
  line-height: 18px;
  margin: 0;
  padding: 0;
}

.header__button.button {
  padding: 10px 5px;
  font-size: 12px;
  line-height: 17px;
}

@media (min-width: 768px) {
  .header {
    padding-top: 0;
    background-color: green;
    background-image: url("../img/bg.jpg");
    background-position: 40% center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;
    height: 665px;
  }
  .header__promo {
    background-image: none;
    background-color: transparent;
  }
  .header__info {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 70px;
  }
  .header__img {
    margin-bottom: 0;
  }
  .header__company {
    margin-bottom: 0;
  }
  .header__company-name {
    font-size: 18px;
    line-height: 20px;
  }
  .header__callback {
    padding-top: 15px;
  }
  .header__link {
    color: #ffffff;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 5px;
  }
}
@media (min-width: 1300px) {
  .header {
    background-position: center center;
  }
  .header__callback {
    padding-left: 85px;
  }
}
.button {
  display: inline-block;
  border: 0;
  font: inherit;
  cursor: pointer;
  text-decoration: none;
  min-width: 140px;
  border-radius: 5px;
  padding: 8px 20px;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #C70101;
}
.button:hover {
  color: #C70101;
  background-color: #ffffff;
  outline: 1px solid #C70101;
}

@media (min-width: 768px) {
  .button {
    font-size: 16px;
    line-height: 24px;
    padding: 12px 40px;
  }
}
.promo {
  text-align: center;
}

.promo__text {
  padding-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}

.promo__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.promo__slogan {
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.promo__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.promo__item {
  position: relative;
  font-size: 14px;
  line-height: 16px;
  padding-top: 65px;
  margin-bottom: 30px;
}
.promo__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-size: contain;
  background-repeat: no-repeat;
}

.promo__item--time::before {
  width: 42px;
  height: 46px;
  background-image: url("../img/timer.svg");
}

.promo__item--delivery::before {
  width: 44px;
  height: 45px;
  background-image: url("../img/app.svg");
}

.promo__item--answers::before {
  width: 45px;
  height: 42px;
  background-image: url("../img/messages.svg");
}

.promo__link {
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: underline;
}
.promo__link:hover {
  color: #C70101;
}

.promo__button {
  margin-bottom: 20px;
}

.promo__go {
  padding: 0;
  padding-bottom: 50px;
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

@media (min-width: 550px) {
  .promo__list {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 500px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .promo__text {
    padding-top: 0;
    text-align: right;
    margin-bottom: 57px;
  }
  .promo__title {
    font-size: 42px;
    line-height: 42px;
  }
  .promo__slogan {
    font-size: 24px;
    line-height: 24px;
  }
  .promo__wrap {
    width: 520px;
    margin-left: auto;
  }
  .promo__button {
    padding: 12px 40px;
    font-size: 16px;
    line-height: 24px;
  }
  .promo__go {
    font-size: 16px;
    line-height: 20px;
    padding-bottom: 40px;
  }
}
@media (min-width: 1300px) {
  .promo {
    text-align: right;
    margin-left: auto;
  }
  .promo__list {
    width: 430px;
    margin: 0;
    padding-right: 20px;
    margin-left: auto;
    margin-bottom: 30px;
    text-align: center;
  }
  .promo__item span {
    display: block;
  }
}
.advantages {
  background-color: #F6F6F6;
  text-align: center;
  padding-top: 20px;
}

.advantages__title {
  font-size: 20px;
  line-height: 20px;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
}

.advantages__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.advantages__item {
  position: relative;
  padding-top: 95px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 70px;
  height: 70px;
  background-position: center;
}

.advantages__text {
  font-family: "Moderno-medium";
  font-size: 16px;
  line-height: 18px;
}

.advantages__item--practice::before {
  background-image: url("../img/cup.svg");
  background-size: contain;
  background-repeat: no-repeat;
}

.advantages__item--health::before {
  background-image: url("../img/heart.svg");
  background-size: contain;
  background-repeat: no-repeat;
}

.advantages__item--run::before {
  background-image: url("../img/shoe.svg");
  background-size: contain;
  background-repeat: no-repeat;
}

.advantages__header {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.advantages__link {
  color: #C70101;
  font-weight: normal;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .advantages {
    padding-top: 55px;
    padding-bottom: 30px;
  }
  .advantages__title {
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 60px;
  }
  .advantages__list {
    margin: 0 auto;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .advantages__item {
    padding-top: 108px;
    width: 30%;
  }
}
.form-section {
  padding-top: 20px;
  padding-bottom: 40px;
  background-color: gray;
  background-image: url("../img/bg-form.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  text-align: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .form-section {
    background-image: url("../img/bg-form@2x.jpg");
    background-size: auto 680px;
  }
}

.form-section__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
}

.form-section__text {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-family: "Moderno-light";
}

@media (min-width: 768px) {
  .form-section {
    padding-top: 85px;
    padding-bottom: 120px;
    text-align: left;
  }
  .form-section__title {
    font-size: 36px;
    line-height: 50px;
    max-width: 790px;
    margin-bottom: 18px;
  }
  .form-section__text {
    font-size: 20px;
    line-height: 26px;
    letter-spacing: 0.08em;
    margin-bottom: 47px;
  }
}
.form {
  max-width: 303px;
  margin: 0 auto;
}

.form__section {
  margin-bottom: 20px;
}

.form__text {
  border: 1px solid rgba(205, 205, 205, 0.5);
  color: #4B4B4B;
  height: 45px;
  padding-left: 10px;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
}
.form__text::placeholder {
  color: #4B4B4B;
}

.form__field {
  margin-bottom: 15px;
}

.form__send {
  width: 100%;
}

@media (min-width: 768px) {
  .form {
    margin: 0;
    margin-right: auto;
  }
}