.form-section {
    padding-top: 20px;
    padding-bottom: 40px;
    background-color: gray;
    background-image: url("../img/bg-form.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;
    text-align: center;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg-form@2x.jpg");
        background-size: auto 680px;
    }     
}

.form-section__title {
    margin: 0;
    margin-bottom: 30px;
    padding: 0; 
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
}    

.form-section__text {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    font-family: "Moderno-light";
}

@media (min-width: $tablet-width) {

    .form-section {
        padding-top: 85px;
        padding-bottom: 120px;
        text-align: left;
    }

    .form-section__title {        
        font-size: 36px;
        line-height: 50px;
        max-width: 790px;
        margin-bottom: 18px;
    }    

    .form-section__text {
        font-size: 20px;
        line-height: 26px;
        letter-spacing: 0.08em;
        margin-bottom: 47px;
    }
}    